import { useEffect, useState } from 'react';
import { RadioGroup,FormControlLabel, Checkbox, MenuItem, InputBase, Select, Radio, Container, Divider, Typography,  Grid, Box, } from '@mui/material';
import Page from 'src/components/Page';
import { useDispatch, useSelector } from 'src/store';
import { toggleTheme, setTheme } from 'src/store/slices/setting'
import { Redirect, useLocation } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import useAuth from 'src/hooks/useAuth';
import FormControlWrapper from 'src/components/FormControlWrapper';
import FormLabel from 'src/components/FormLabel';
import languages from 'src/assets/json/language.json'
import ProfileLayout from 'src/views/common/ProfileLayout'
import _ from 'lodash'

const style = {
  verifyText: {
    color: '#22C55E'
  },
  fontWeightBold: {
    fontWeight: 'bold'
  },
  backgroundWhite: {
    backgroundColor: '#fff'
  },
  errorInfo: {
    backgroundColor: '#168bc226',
    borderRadius: '5px'
  },
  container: {
    width: '100%',
    maxWidth: 360,
    bgcolor: 'background.paper'
  }
};

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  'label + &': {
    marginTop: theme.spacing(3)
  },
  '& .MuiInputBase-input': {
    marginTop: '6px',
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '8px 26px 8px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)'
    }
  }
}));

const Profile = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const { theme } = useSelector((state) => state.setting);

  const { isAuthenticated } = useAuth();
  if (isAuthenticated !== null && isAuthenticated === false) {
    const system = location.pathname.split('/')[1];
    const returnUrl = new URLSearchParams(location.search).get('returnUrl');
    return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}/login?returnUrl=/${system}/profile`} />;
  }

  

  return (
    <ProfileLayout>
      <Typography variant="h6" component="b">
        User Preferences
      </Typography>
      <Divider />
      <Box sx={{ display: 'flex' }}>
        <FormControlWrapper>
          <FormLabel label="Language" />
          <Select
            fullWidth
            input={<BootstrapInput />}
            defaultValue="en"
          >
            {languages
              ? languages.map((x) => {
                  return <MenuItem value={x.code} key={x.code}>{x.value}</MenuItem>;
                })
              : ''}
        </Select>
        </FormControlWrapper>
        <FormControlWrapper marginLeft={5}>
          <FormLabel label="Notifications" />
          <FormControlLabel control={<Checkbox />} label="Assets transfer update" />
        </FormControlWrapper>
      </Box>
      <Box>
        <FormControlWrapper>
          <RadioGroup
            value={theme}
            onChange={($event) => dispatch(setTheme($event.target.value))}
            name="themes-mode"
          >
            <FormLabel label='Themes' />
            <FormControlLabel control={<Radio />} label="Dark Mode" value="dark" />
            <FormControlLabel control={<Radio />} label="Light Mode" value="light" />
          </RadioGroup>
        </FormControlWrapper>
      </Box>
      <Box>
        <FormControlWrapper>
          <FormLabel label="Privacy" />
          <FormControlLabel control={<Checkbox />} label="Display your email address to other users?" />
          <br/>
          <FormControlLabel control={<Checkbox />} label="Display your phone number to other users?" />
        </FormControlWrapper>
      </Box>
    </ProfileLayout>
  );
};

export default Profile;
